import React from 'react';
import styled from 'styled-components';


import { CheckboxTypes } from '../../enums/CheckboxTypes';
import colors from '../../constants/colors';

import Icon from '@mdi/react';
import { mdiCheck, mdiCheckboxBlank, mdiClose } from '@mdi/js';

const Input = styled.input`
    opacity: 0;
`;

const Label = styled.label``;

const Box = styled.div`
    border: 1px solid  ${colors.grayDark};
    border-radius: 0.25rem;
    background-color: ${colors.background};
`;




export interface CheckboxProps {
    checkboxType: CheckboxTypes,
    children: string | Node,
    checked: boolean,
    value: string,
    onChange(): void
}

const iconFromCheckboxType = (type: CheckboxTypes) => {
    switch (type) {
        case CheckboxTypes.check:
            return mdiCheck;
        case CheckboxTypes.cross:
            return mdiClose;
        default:
            return mdiCheckboxBlank;
    }
}

const Checkbox = ({
    checkboxType,
    checked,
    children,
    value,
    onChange, // do we need an on
}: CheckboxProps) => {
    const iconPath = iconFromCheckboxType(checkboxType)

    return(
        <div>
            <Box>
                <Icon path={iconPath}></Icon>
            </Box>
            <Input type='checkbox' value={value} onChange={onChange} checked={checked}></Input>
            <Label>{children}</Label>
        </div>
    );
};

export default Checkbox;